import Navbar from "./components/Navbar";
import React, { useState, useEffect } from 'react'
import Cryptos from "./components/Cryptos";
import Watchlist from "./components/Watchlist";


function App() {
  const [tab1, setTab1] = useState(true)
  const [tab2, setTab2] = useState(false)
  return (
    <div className="App">
      <Navbar />
      <div className="btns">
        <button onClick={()=>{setTab1(true);setTab2(false)}} className={tab1 ? 'active':undefined}>Cryptos (in USD $)</button>
        <button onClick={()=>{setTab2(true);setTab1(false)}} className={tab2 ? 'active':undefined}>Cryptos (in INR ₹)</button>
      </div>
      <main>
        {tab1 && <Cryptos />}
        {tab2 && <Watchlist />}
      </main>
    </div>
  );
}

export default App;
