import React, { useState, useEffect } from 'react'

function Cryptos() {
  const [data, setData] = useState([])
  const getData = async () => {
    const res = await fetch(
      `https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=100&page=1&sparkline=false`,
    )
    const data = await res.json()
    setData(data)
  }

  useEffect(() => {
    getData()
    console.log(data)
  }, [])

  
  const watchHandler = (id,e) =>{
    const elm = data.find(data=>data.id===id)
    console.log(elm)
    console.log(e.target)
  }
  return (
    <section>
      <ul>
        {data.map((data) => {
          const {
            id,
            symbol,
            name,
            image,
            current_price,
            price_change_percentage_24h,
          } = data
          const changePercent=Math.round((price_change_percentage_24h + Number.EPSILON) * 100) / 100 
          return (
            <li key={id}>
              <img src={image} alt={symbol} />
              <h2 className="name">{name} <br /><span>{symbol}</span> </h2>
              <h3 className="price">${current_price}<br/><span>{changePercent>0 && '+'}{changePercent}%</span></h3>
              <img style={{"--i":changePercent}} className={changePercent>0 ? "watch":"active watch"} onClick={(e)=>watchHandler(id,e)}></img>
            </li>
          )
        })}
      </ul>
    </section>
  )
}

export default Cryptos
